<template>
  <div class="pageContent">
    <!-- 筛选条件 -->
    <el-card class="filter-container" shadow="never">
      <el-tabs v-model="searchList.tabType" @tab-click="getData">
        <el-tab-pane label="所有活动" name="-1"></el-tab-pane>
        <el-tab-pane label="未开始" name="0"></el-tab-pane>
        <el-tab-pane label="进行中" name="1"></el-tab-pane>
        <el-tab-pane label="已结束" name="2"></el-tab-pane>
        <el-tab-pane label="已失效" name="3"></el-tab-pane>
      </el-tabs>
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small">重置</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="活动名称：">
            <el-input style="width: 203px" v-model="searchList.name" clearable placeholder="活动名称"></el-input>
          </el-form-item>
          <el-form-item label="开始日期：">
            <el-date-picker
              v-model="searchList.beginTime"
              value-format="yyyy-MM-dd HH:mm:ss"
              type="datetime"
              placeholder="选择开始时间"
              align="right"
              :picker-options="pickerOptions">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="结束日期：">
            <el-date-picker
              v-model="searchList.endTime"
              value-format="yyyy-MM-dd HH:mm:ss"
              type="datetime"
              placeholder="选择结束时间"
              align="right"
              :picker-options="pickerOptions">
            </el-date-picker>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 - head-->
    <el-card class="operate-container" style="margin-top:10px" shadow="never">
      <div class="flex-row-between" style="width:100%;">
        <div>
          <i class="el-icon-tickets"></i>
          <span>数据列表</span>
        </div>
        <div style="padding-right:20px;">
          <el-button type="text" icon="el-icon-circle-plus-outline" @click="$router.push({path:'/marketManage/discount/newDiscount'})">新增限时折扣</el-button>
        </div>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <div>
      <el-card style="margin-top:20px;" class="filter-container" shadow="never">
        <el-table :data="list" border style="width: 100%">
          <el-table-column prop="name" label="活动名称" width="300"></el-table-column>
          <el-table-column label="活动时间" width="450">
            <template slot-scope="scope">
              {{scope.row.activityBeginTime + ' 至 ' + scope.row.activityEndTime}}
            </template>
          </el-table-column>
          <el-table-column label="活动状态" width="140">
            <template slot-scope="scope">
              <div style="color:#E6A23C;" v-if="scope.row.activityStatus == 0">未开始</div>
              <div style="color:#409EFF;" v-if="scope.row.activityStatus == 1">进行中</div>
              <div style="color:#F56C6C;" v-if="scope.row.activityStatus == 2">已结束</div>
              <div style="color:#F56C6C;" v-if="scope.row.activityStatus == 3">已失效</div>
            </template>
          </el-table-column>
          <el-table-column width="140">
            <template slot="header" slot-scope="scope">
              <span :data-scope="scope.row">订单总金额</span>
              <el-tooltip class="item" effect="dark" content="活动订单总付款金额（包含退款）" placement="top-start">
                <i class="el-icon-question"></i>
              </el-tooltip>
            </template>
            <template slot-scope="scope">
              {{scope.row.orderAmount}}
            </template>
          </el-table-column>
          <el-table-column width="140">
            <template slot="header" slot-scope="scope">
              <span :data-scope="scope.row">付款订单数</span>
              <el-tooltip class="item" effect="dark" content="参与活动并付款的订单（包含退款订单）" placement="top-start">
                <i class="el-icon-question"></i>
              </el-tooltip>
            </template>
            <template slot-scope="scope">
              {{scope.row.orderTotal}}
            </template>
          </el-table-column>
          <el-table-column width="140">
            <template slot="header" slot-scope="scope">
              <span :data-scope="scope.row">付款人数</span>
              <el-tooltip class="item" effect="dark" content="参与活动并付款的人数（包含退款人数）" placement="top-start">
                <i class="el-icon-question"></i>
              </el-tooltip>
            </template>
            <template slot-scope="scope">
              {{scope.row.userTotal}}
            </template>
          </el-table-column>
          <el-table-column prop="productTotal" label="参与活动商品数" width="140"></el-table-column>
          <el-table-column
            label="操作"
            width="180">
            <template slot-scope="scope">
              <el-button @click="$router.push({path:'/marketManage/discount/discountInfo',query:{id:scope.row.id}})" type="text" size="small">查看</el-button>
              <el-button @click="$router.push({path:'/marketManage/discount/editDiscount',query:{id:scope.row.id}})" v-if="scope.row.activityStatus == 1 || scope.row.activityStatus == 0" type="text" size="small">修改</el-button>
              <el-button @click="failItem(scope.row)" v-if="scope.row.activityStatus == 1" style="color:#F56C6C;" type="text" size="small">使失效</el-button>
              <el-button @click="deleteItem(scope.row.id)" v-if="scope.row.activityStatus == 0" style="color:#F56C6C;" type="text" size="small">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="flex-row-end" style="padding-top:20px;">
          <div>
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :page-sizes="[5,10,15,20]"
              :page-size="searchList.pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="listTotal">
            </el-pagination>
          </div>
        </div>
      </el-card>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getShopActivityList , deleteShopActivity , invalidShopActivity } from '@/api/marketManage/discount'
export default {
  data(){
    return{
      searchList:{
        tabType:'-1',
        pageNum:1,
        pageSize:5
      },
      category:[],
      list:[],
      listTotal:0,
      applicationTime: '',
      pickerOptions: {
        shortcuts: [{
          text: '今天',
          onClick(picker) {
            picker.$emit('pick', new Date());
          }
        }, {
          text: '昨天',
          onClick(picker) {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24);
            picker.$emit('pick', date);
          }
        }, {
          text: '一周前',
          onClick(picker) {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', date);
          }
        }]
      },
      brandFold: false,
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    this.searchList.activityType = 1
    this.getData();
  },
  methods:{
    // 列表 - 获取数据
    getData(){
      this.searchList.activityStatus = this.searchList.tabType == '-1' ? '' : this.searchList.tabType
      console.log('请求的数据',this.searchList)
      getShopActivityList(this.searchList).then(res=>{
        console.log('活动列表',res)
        if(res.code == 200){
          this.list = res.data.list
          this.listTotal = res.data.total
        }
      })
    },
    // 搜索栏 - 重置
    reset(){
      this.searchList.name = ''
      this.searchList.beginTime = ''
      this.searchList.endTime = ''
      this.getData()
    },
    // 已开始 - 失效
    failItem(o){
      this.$confirm('确定让这组限时折扣活动失效吗？失效后， 活动商品将提前结束活动，恢复至原价；买家之前已购买的限时折扣商品正常下单，待付款订单也可正常提交。', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let obj = {
          id:o.id,
          userId:this.user.userId,
          shopId:this.shop.shopId,
          name:o.name
        }
        invalidShopActivity(obj).then(res=>{
          console.log('失效操作',res)
          if(res.code == 200){
            this.$message({
              type: 'success',
              message: res.message
            });
            this.getData();
          }else{
            this.$message.error(res.message);
          }
        })
      }).catch(() => {});
    },
    // 未开始 - 删除
    deleteItem(id){
      this.$confirm('确定删除此限时折扣活动?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        deleteShopActivity({id:id,userId:this.user.userId}).then(res=>{
          console.log('删除操作',res)
          if(res.code == 200){
            this.$message({
              type: 'success',
              message: res.message
            });
            this.getData();
          }else{
            this.$message.error(res.message);
          }
        })
      }).catch(() => {});
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val
      this.getData();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val
      this.getData();
    }
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .checkBoxDiv{
    font-size: 14px;
    margin-left: 40px;
    line-height: 19px;
    color: #606266;
  }
  .active {
    height: 70px;
  }
</style>